import { h } from 'preact'
import { useState, useEffect } from 'preact/hooks'

import { root, isScrolled, mobileMenu, displayNone } from './Header.module.scss'

const scrollTo = (where) => (e) => {
	e.preventDefault();
	document.querySelector(`[name="${where}"]`).scrollIntoView();
}

const links = [
	<a href="# " onClick={scrollTo('why')}>Why</a>,
	<a href="# " onClick={scrollTo('how')}>How</a>,
	<a href="# " onClick={scrollTo('apps')}>Apps</a>,
	<a href="# " onClick={scrollTo('about')}>About</a>,
	<a href="# " onClick={scrollTo('contact')}>Contact</a>,
]

export default function Header() {

	const [scrolled, setScrolled] = useState(false)
	const [mobileOpen, setMobileOpen] = useState(false)

	useEffect(() => {
		//not home
		if (window.location.pathname.length > 1) {
			setScrolled(true)
			return
		}
		let timeout;
		window.onscroll = () => {
			if (timeout) clearTimeout(timeout)
			timeout = setTimeout(() => setScrolled(window.scrollY !== 0), scrolled ? 100 : 0)
		}
	}, [])

	return (
		<div className={[root, scrolled ? isScrolled : ''].join(' ')}>

			<header>
				<h3>Hashcache</h3>
				<div>
					{links}
				</div>
				<i onClick={() => setMobileOpen(!mobileOpen)} className="fas fa-bars fa-lg"></i>
			</header>

			<div
				onClick={() => setMobileOpen(!mobileOpen)}
				className={mobileOpen ? mobileMenu : displayNone}
			>
				<i className="fas fa-times"></i>
				{links}
			</div>

		</div>
	)
}