import { h } from 'preact'
import { useEffect } from 'preact/hooks'

import Hero from './../../components/Hero/Hero'
import How from './../../img/how.png'
import Apps from './../../img/apps.png'
import B2 from './../../img/backblaze-logo.jpg'
import CF from './../../img/cloudflare-logo.jpg'
import AWS from './../../img/aws-logo.png'
import { route, root, center, singleCenter, fullWidth, apps, logo } from './Home.module.scss'


export default function Home() {

	useEffect(() => {
		window.scrollTo(0, 0)
	}, [])

	return (
		<div className={[route, root].join(' ')}>

			<Hero />

			<section className={singleCenter}>
				<a name="why"></a>
				<div style={{padding: 16, border: '2px solid #ddd'}}>
				<h1>Why Hashcache?</h1>
				<p>You like designing, developing and delivering amazing products.</p>
				<p>You <em>don't</em> like worrying about security or compliance.</p>
				<p>Hashcache will reduce:</p>
				<ul>
					<li>Threat of data breach</li>
					<li>Development and security costs</li>
					<li>Legal cost of compliance (or lack thereof)</li>
				</ul>
				<p>Spend resources on making your product and benefit from increased trust with customers!</p>
				</div>
			</section>

			<section className={singleCenter}>
				<a name="how"></a>
				<h1>How it Works</h1>
				<p>Alice (A) and Bob (B) want to use your messaging feature to discuss some confidential business.</p>
				<img src={How} alt="how it works" />
			</section>
			
			<section className={singleCenter}>
				<h1>Example: Spreadsheets</h1>
				<ol>
					<li>Customers secure and private data</li>
					<li>You want metris such as daily active users, AB tests, ...</li>
				</ol>
				<img src={Apps} alt="applications" />
				<p><strong>Solution</strong></p>
				<p>Hashcache secures <em>only</em> the cells of spreadsheets containing sensitive raw data before uploading to the cloud.</p>
				<p>Your app can still leverage metadata such as rows, columns, filenames and access patterns.</p>
				<p><strong>Key Points</strong></p>
				<ul>
					<li>With <em>less</em> sensitive data stored in your database, you save on security and compliance costs.</li>
					<li>Your customers are now the <em>only</em> access point for their sensitive data, reducing the likelihood of a breach and increasing trust in your service.</li>
				</ul>
			</section>
			
			<section className={[fullWidth, apps].join(' ')}>
				
				<section className={fullWidth}>
					<a name="apps"></a>
					<h1>Applications</h1>
				</section>

				<div className={center}>
					<h3>Messaging</h3>
					<i class="fas fa-comments fa-4x"></i>
				</div>
				<div className={center}>
					<h3>Backups</h3>
					<i class="fas fa-copy fa-4x"></i>
				</div>
				<div className={center}>
					<h3>Fintech</h3>
					<i className="fas fa-file-invoice-dollar fa-4x"></i>
				</div>
				<div className={center}>
					<h3>Healthcare</h3>
					<i className="fas fa-notes-medical fa-4x"></i>
				</div>
				
			</section>
			
			<section>
				<h1>Problem</h1>

				<ul>
					<li>Customers are increasingly skeptical about the handling of their data</li>
					<li>Data hacks, disgruntled employees and unauthorized selling of data are growing concerns</li>
					<li>A breach can encrypt or extract <em>all</em> data</li>
				</ul>
			</section>

			<section>
				<h1>Solution</h1>
				<ul>
					<li>Customers secure their sensitive data individually and on their own devices before uploading to the cloud</li>
					<li>You reduce sensitive data liabilities, security costs and compliance burdens</li>
					<li>A breach does <em>not</em> reveal any sensitive customer data</li>
				</ul>
			</section>

			<section>
				<h1>What is Hashcache?</h1>
				<p>Hashcache is a data API for businesses that removes security and compliance risk and makes privacy a competitive advantage.</p>
				<p>Developers obtain an API key and install a small JavaScript library. That's it!</p>
				<p>See the code sample above for how easy it is to work with Hashcache.</p>
				<p>Currently an invite only beta. <a href="mailto:hashcache@protonmail.com">	Contact</a> if you are interested.</p>
			</section>

			<section>
				<h1>Customer Experience</h1>
				<p>Hashcache was designed with developers in mind, but also their customers.</p>
				<p>As a developer you can create unique accounts for your customers, without revealing any personal information to Hashcache.</p>
				<p>Customers only have to set a "Data 2 Factor" (D2F) password that protects their data from anyone they do not share it with.</p>
			</section>

			<section className={[fullWidth, logo].join(' ')}>
				<section className={fullWidth}>
					<h1>Technology</h1>
				</section>

				<div>
					<img src={CF} alt="cloudflare" />
				</div>

				<div>
					<img src={B2} alt="backblaze" />
				</div>

				<div>
					<img src={AWS} alt="amazon web services" />	
				</div>
			</section>

			<section>
				<a name="about"></a>
				<h1>About</h1>
				<p>
					Hashcache is developed in Vancouver, B.C. by <a href="https://twitter.com/mattdlockyer">Matt Lockyer</a>.
				</p>
			</section>

			<section>
				<a name="contact"></a>
				<h1>Contact</h1>
				<p><a href="mailto:hashcache@protonmail.com">hashcache@protonmail.com</a></p>
			</section>

		</div>
	)
}