import { h } from 'preact'

import {
	hero, background, section,
} from './Hero.module.scss'

const code = `const data = { msg: "hello world!" }
const tags = ["example", "message"]
// share with users "Alice" and "Bob"
const accounts = ["Alice", "Bob"]
await hashcache.store({ data, tags, accounts })
...
const results = await hashcache.fetch({ tags })
console.log(results[0].data)
// { msg: "hello world!" }
`

export default function Hero() {

	return (
		<div className={hero}>

			<section className={[section, background].join(' ')}>
				<h1>Worry Free Data</h1>
				<p>
					Security risks and compliance burdens have made storing customer data a liability.
				</p>
				<p>
					Secure their sensistive data before it touches the cloud.
				</p>
				<p>
					Integrate seamlessly with just a few lines of code.
				</p>
				{/* <button>Get Started</button> */}
			</section>

			<section className={section}>
				<div>
					<pre>
						<code className="language-javascript">
							{code}
						</code>
					</pre>
				</div>
			</section>

		</div>
	)
}