import { h } from 'preact'
import Router from 'preact-router';


import Header from './components/Header/Header'
import Footer from './components/Footer/Footer'
import Home from './routes/Home/Home'

export default function App() {

  return ([
    <Header />,
    <Router>
      <Home path="/" />
    </Router>,
    <Footer />,
  ])
}